import * as React from "react";
import AppBar from "@mui/material/AppBar";
import SendIcon from "@mui/icons-material/Send";
import CssBaseline from "@mui/material/CssBaseline";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import { styled, alpha } from "@mui/material/styles";
import SearchIcon from "@mui/icons-material/Search";
import InputBase from "@mui/material/InputBase";
import FileCopyIcon from "@mui/icons-material/FileCopy";
import { CopyToClipboard } from "react-copy-to-clipboard";
import nodataImage from "./nodata.jpg";
import blankpage from "./blankpage.jpg";
import searching from "./searching.jpg";
import {
  Avatar,
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  IconButton,
  LinearProgress,
  Snackbar,
  Stack,
} from "@mui/material";
import PDFViewer from "./PdfViewer";

const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(1),
    width: "auto",
  },
}));

function HighlightedText({ text, highlightWords }) {
  const words = text.split(" ");

  // Function to check if a word matches any of the highlightWords
  const isHighlighted = (word) => highlightWords.includes(word);

  return (
    <Typography>
      {words.map((word, index) => (
        <span
          key={index}
          style={{
            fontWeight: isHighlighted(word) ? "bold" : "normal",
            backgroundColor: isHighlighted(word) ? "yellow" : "transparent",
          }}
        >
          {word}
          {index < words.length - 1 ? " " : ""}
        </span>
      ))}
    </Typography>
  );
}

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    width: "100%",
  },
}));

function CopyCard({
  pdf_name,
  raw_text_data,
  page_number,
  highlightWords,
  score,
  handelOpenPdf,
}) {
  const [copied, setCopied] = React.useState(false);
  const [copiedText, setCopiedText] = React.useState("");
  const [openSnackbar, setOpenSnackbar] = React.useState(false);

  const handleCopy = (text) => {
    setCopied(true);
    setCopiedText(text);
    setOpenSnackbar(true);
    setTimeout(() => {
      setCopied(false);
    }, 1500); // Reset copied state after 1.5 seconds
  };

  const handleCloseSnackbar = () => {
    setOpenSnackbar(false);
  };

  return (
    <Card sx={{ m: 1 }}>
      <CardHeader
        action={
          <CopyToClipboard text={pdf_name} onCopy={() => handleCopy(pdf_name)}>
            <IconButton
              color={copied ? "primary" : "default"}
              onClick={() => handleCopy(pdf_name)}
            >
              <FileCopyIcon />
            </IconButton>
          </CopyToClipboard>
        }
        avatar={
          <Avatar
            sx={{ bgcolor: "#00181c" }}
            aria-label="recipe"
            onClick={() => handelOpenPdf(pdf_name, page_number)}
          >
            {page_number}
          </Avatar>
        }
        title={pdf_name}
        subheader={"score : " + score}
        sx={{
          backgroundColor: "#e2ecf5", // Change this to your desired header color
          color: "#11061e", // Change this to your desired text color
        }}
      />

      <CardContent sx={{ mt: 0, pt: 0 }}>
        <HighlightedText
          text={raw_text_data}
          highlightWords={highlightWords}
        ></HighlightedText>
      </CardContent>

      <Snackbar
        open={openSnackbar}
        autoHideDuration={1500}
        onClose={handleCloseSnackbar}
        message={`Copied to clipboard: ${copiedText}`}
      />
    </Card>
  );
}

const SearchResult = ({ searchData, highlightWords, handelOpenPdf }) => {
  return (
    <Box sx={{ pb: "200px" }}>
      {searchData.map(({ pdf_name, page_number, raw_text_data, score }) => (
        <React.Fragment key={pdf_name + page_number}>
          <CopyCard
            pdf_name={pdf_name}
            page_number={page_number}
            raw_text_data={raw_text_data}
            score={score}
            highlightWords={highlightWords}
            handelOpenPdf={handelOpenPdf}
          />
        </React.Fragment>
      ))}
    </Box>
  );
};

const MemoizedSearchResult = React.memo(SearchResult);

export default function Home() {
  const [queryExact, setQueryExact] = React.useState("");
  const [query, setQuery] = React.useState("");
  const [searchData, setSearchData] = React.useState([]);
  const [loading, setLoading] = React.useState(false);
  const [highlightWords, setHighlightWords] = React.useState([]);
  const [isSearch, setIsSearch] = React.useState(false);

  const [pdfName, setPdfName] = React.useState("Sanket-Agarwal-HL1");
  const [pdfOpen, setPdfOpen] = React.useState(false);
  const [openPdfPageNumber, setOpenPdfPageNumber] = React.useState(3);

  const pdfClose = () => {
    setPdfOpen(false);
  };

  const handelOpenPdf = (pdf_name, page) => {
    //setPdfName(pdf_name);
    setOpenPdfPageNumber(page);
    setPdfOpen(true);
  };

  const handleChange = (event) => {
    setIsSearch(false);
    setSearchData([]);
    const { name, value } = event.target;
    if (name === "queryExact") {
      setQueryExact(value);
    } else if (name === "query") {
      setQuery(value);
    }
  };

  const search = () => {
    // Make the API request
    setSearchData([]);
    setLoading(true);
    setIsSearch(true);
    fetch(
      `https://haikent.com/search_api.php?query_exact=${queryExact}&query=${query}`
    )
      .then((response) => response.json())
      .then((data) => {
        setSearchData(data);
        setLoading(false);
        setHighlightWords(queryExact.split(" ").concat(query.split(" ")));
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
        setLoading(false);
      });
  };

  return (
    <React.Fragment>
      <CssBaseline />
      <PDFViewer
        open={pdfOpen}
        pdf_name={pdfName}
        page={openPdfPageNumber}
        onClose={pdfClose}
      ></PDFViewer>

      {!isSearch && (
        <img
          src={blankpage}
          alt="Blank Page"
          style={{ maxWidth: "100%", maxHeight: "100%" }}
        />
      )}

      {loading && (
        <img
          src={searching}
          alt="Loading"
          style={{ maxWidth: "100%", maxHeight: "100%" }}
        />
      )}

      {isSearch && searchData.length === 0 && loading === false && (
        <img
          src={nodataImage}
          alt="No Data "
          style={{ maxWidth: "100%", maxHeight: "100%" }}
        />
      )}

      <MemoizedSearchResult
        searchData={searchData}
        highlightWords={highlightWords}
        handelOpenPdf={handelOpenPdf}
      />
      <AppBar
        position="fixed"
        sx={{ backgroundColor: "#272213", top: "auto", bottom: 0 }}
      >
        {loading && <LinearProgress />}
        <Toolbar>
          <Stack spacing={2} sx={{ mt: 1, mb: 1, flexGrow: 1 }}>
            <Search>
              <SearchIconWrapper>
                <SearchIcon />
              </SearchIconWrapper>
              <StyledInputBase
                fullWidth
                name="queryExact"
                value={queryExact}
                onChange={handleChange}
                placeholder="Syntax ( वाक्य ) "
                inputProps={{ "aria-label": "search" }}
              />
            </Search>

            <Search>
              <SearchIconWrapper>
                <SearchIcon />
              </SearchIconWrapper>
              <StyledInputBase
                name="query"
                fullWidth
                value={query}
                onChange={handleChange}
                placeholder="Word ( शब्द ) "
                inputProps={{ "aria-label": "search" }}
              />
            </Search>

            <Button
              disabled={loading}
              variant="contained"
              endIcon={<SendIcon />}
              onClick={() => search()}
              sx={{ backgroundColor: "#755929" }}
            >
              Search
            </Button>
          </Stack>
        </Toolbar>
      </AppBar>
    </React.Fragment>
  );
}
